import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';


const ChilliHotComponent = () => {

	const dispatch = useDispatch();
	const App = useSelector(state => state.app);

	/// Return to the scroll position that has been saved for this page.
	useEffect(() => {
		const sp = App.appsList[App.currentApp].scrollPos
		const scrollPosition = (sp) ? sp : 0;
		window.scrollTo(0, scrollPosition);
	}, [App]);

	/// Save window scroll position, then change to new app.
	const handleClick = (e) => {
		dispatch({ type: 'SAVE_SCROLL_POS', payload:{scrollPos: window.scrollY, currentApp:App.currentApp} })
		const item = (e.target.hasAttribute('link')) ? e.target.getAttribute('link') : e.target.parentNode.getAttribute('link');
		dispatch ({ type:'CHANGE_APP_STATE', payload: item })
	}

	const itemsList0 = [
		{link:"Hacker News", clas:'hckr', content: "Hacker News"},
		{link:"reddit", clas:'rddt', content: "Reddit"},
		{link:"Shopping", clas:'shpg', content: "Shopping"},
		{link:"Comments", clas:'msgg', content: "Contentful"},
		{link:"RestDBComments", clas:'rdbio', content: "RestDBio"},
		{link:"PDFComponent", clas:'pdfvr', content: "PDFViewer"},
		{link:"Assets", clas:'asst', content: "Assets"}
	]

	const itemsList = itemsList0.map((b,i) => {
		const cl = 'gallery-box logo ' + b.clas
		return (
				<div key={i} link={b.link} >
					<div className={cl}></div>
					<div>{b.content}</div>
				</div>
			)
	});

	return (
		<div id='chilli' className='container-center' onClick = {handleClick}>
			{ itemsList }
		</div>
		)
}

export default ChilliHotComponent
