const pdfjsLib = window['pdfjs-dist/build/pdf'];
const fallbackUrl = 'https://assets.ctfassets.net/ji9dy7eny2j0/3hzsinotXLwhgku4mdvTcw/dfbd844457cbaf0210da7d8d11016f69/Electronics.pdf'


export const showPdf = (cardID, url = fallbackUrl) => {

	const loadingTask = pdfjsLib.getDocument(url);
	loadingTask.promise.then(
	  async (pdf) => {
			for(let i = 0; i < pdf.numPages; i++) {

				if(i > 20) return;

				const pageNumber = i + 1;
				const page = await pdf.getPage(pageNumber)

				const scale = 1;
				const viewport = page.getViewport({ scale: scale, });

				const d = document.createElement('div')
				const canvas = document.createElement('canvas')
				d.appendChild(canvas)
				document.querySelector('.pdf-card').appendChild(d)

		    const context = canvas.getContext('2d');
		    canvas.height = viewport.height;
		    canvas.width = viewport.width;

		    // Render PDF page into canvas context
		    const renderContext = {
		      canvasContext: context,
		      viewport: viewport
		    };

				page.render(renderContext);
			}
	  },
	  function(reason) {
	    // PDF loading error
	    console.error(reason);
	  }
	);
}
