import React from 'react';
import { useSelector } from 'react-redux';
import { showPdf } from './showPdf'


const PDFComponent = () => {

	React.useEffect(() => {
		/// Make the Display 100% wide rather than .container class' default 90%
		document.querySelector('#Display').classList.add('DisplayContainer')
		document.querySelector('#Display').style.paddingTop =  '0';
		document.querySelector('#Display').style.paddingBottom =  '0';
		document.querySelector('body').style.backgroundColor =  '#424242';

		/// When a functional component *un-mounts*, the logic inside the return function will get executed.
		/// So remember to clean up your code if necessary by returning a function inside the useEffect function.
		return () => {
			document.querySelector('#Display').classList.remove('DisplayContainer')
			document.querySelector('#Display').style.paddingTop = '20px'
			document.querySelector('#Display').style.paddingBottom = '30px'
			document.querySelector('body').style.backgroundColor =  'cyan';
		}
  }, []);

	const cardID = 'pdf-card'
	const url = useSelector(state => state.app.appsList.PDFComponent.searchTerm);

	showPdf(cardID, url)

	return (
	    <div className="pdf-section-container">
	      <div className="pdf-card" id='pdf-card'> </div>
	    </div>
	)

}

export default PDFComponent

